.Item-view-img-cont {
  display: flex; }

.items-view {
  width: 70%; }
  .items-view-buttons {
    display: inline-block;
    position: relative;
    margin: 30px; }
  .items-view-button {
    width: 14px;
    height: 14px;
    position: absolute;
    z-index: 2;
    border-color: transparent;
    background-color: transparent; }
  .items-view-icon {
    position: absolute;
    z-index: 0; }
  .items-view-stats {
    margin-left: 30px; }
    .items-view-stats-text--bold {
      font-weight: 800; }
